import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { environment } from "src/environments/environment";
import { Observable, of, throwError } from "rxjs";
import { switchMap, catchError, map } from "rxjs/operators";
import { OuvrirCaisseRequest, CaisseResponse, FermerCaisseRequest } from "../../MODELS/CaisseModel/caisse.model";
import { UsersService } from "../users.service";

@Injectable({
  providedIn: 'root'
})
export class PosCaisseService {
  private apiUrl = environment.apiBaseUrl;
  
  constructor(
    private http: HttpClient,
    private usersService: UsersService
  ) { }

  ouvrirCaisse(request: OuvrirCaisseRequest): Observable<CaisseResponse> {
    return this.usersService.getValidAccessToken().pipe(
      switchMap(token => {
        if (!token) {
          throw new Error('Aucun token trouv');
        }

        const headers = new HttpHeaders({
          'Authorization': `Bearer ${token}`
        });

        return this.http.post<CaisseResponse>(
          `${this.apiUrl}/ouvrir`, 
          request, 
          { headers }
        );
      }),
        catchError(error => {
            console.error('Erreur dtaille', error);
            
            // Essayez d'extraire le message d'erreur spcifique
            let errorMsg = 'Erreur lors de l\'ouverture de la caisse';
            
            if (error.error && error.error.error) {
                errorMsg = error.error.error;
            } else if (error.error && error.error.message) {
                errorMsg = error.error.message;
            } else if (error.message) {
                errorMsg = error.message;
            }
            
            return throwError(() => ({ 
                message: errorMsg,
                originalError: error 
            }));
        })
    );
  }

fermerCaisse(boutiqueId: number): Observable<any> {
  console.log('Tentative de rcupration du token...'); // Log avant de rcuprer le token

  return this.usersService.getValidAccessToken().pipe( // On rcupre le token valide de l'utilisateur
    switchMap(token => {
      if (!token) {
        console.error('Aucun token trouv');
        throw new Error('Aucun token trouv');
      }

      console.log('Token rcupr :', token); // Log du token rcupr

      const headers = new HttpHeaders({
        'Authorization': `Bearer ${token}` // Envoi du token d'authentification
      });

      // Prparation du corps de la requte avec boutiqueId
      const body = {
        boutiqueId: boutiqueId
      };

      console.log('Envoi de la requte POST pour fermer la caisse...'); // Log avant de faire la requte

      return this.http.post<any>(`${this.apiUrl}/fermer`, body, { headers }).pipe(
        catchError(error => {
          console.error('Erreur lors de la requte pour fermer la caisse:', error);
          return throwError(() => error);
        })
      );
    }),
    catchError(error => {
      console.error('Erreur dtaille dans le service:', error);

      // Gestion des erreurs
      let errorMsg = 'Erreur lors de la fermeture de la caisse';
      if (error.error && error.error.error) {
        errorMsg = error.error.error;
      } else if (error.error && error.error.message) {
        errorMsg = error.error.message;
      } else if (error.message) {
        errorMsg = error.message;
      }

      return throwError(() => ({
        message: errorMsg,
        originalError: error
      }));
    })
  );
}

  getDerniereCaisseVendeur(boutiqueId: number): Observable<CaisseResponse> {
    return this.usersService.getValidAccessToken().pipe(
      switchMap(token => {
        if (!token) throw new Error('Aucun token trouv');

        const headers = new HttpHeaders({ 'Authorization': `Bearer ${token}` });

        // pos-caisse-service.ts (extrait)
        return this.http.get<CaisseResponse>(`${this.apiUrl}/caisse/derniere/${boutiqueId}`, { headers }).pipe(
          map(resp => resp),
          catchError(err => {
            console.error('HTTP getDerniereCaisseVendeur error ->', err);

            let msg = 'Erreur inconnue';
            if (err?.error) {
              if (typeof err.error === 'string') {
                msg = err.error;                     // serveur renvoie directement un texte
              } else if (err.error?.message) {
                msg = err.error.message;             // structure { message: "..." }
              } else {
                try { msg = JSON.stringify(err.error); } catch { msg = String(err.error); }
              }
            } else if (err?.message) {
              msg = err.message;
            } else {
              try { msg = JSON.stringify(err); } catch { msg = String(err); }
            }

            // Propager une string (vite new Error(obj) qui devient "[object Object]")
            return throwError(() => msg);
          })
        );

      })
    );
  }

  getCaissesByBoutique(boutiqueId: number): Observable<CaisseResponse[]> {
    return this.usersService.getValidAccessToken().pipe(
      switchMap(token => {
        if (!token) throw new Error('Aucun token trouv');

        const headers = new HttpHeaders({
          'Authorization': `Bearer ${token}`
        });

        return this.http.get<CaisseResponse[]>(
          `${this.apiUrl}/boutique/${boutiqueId}/caisses`, 
          { headers }
        );
      }),
      catchError(error => {
        // Gestion d'erreur dtaille
        let errorMsg = 'Erreur lors du chargement des caisses';
        if (error?.error?.error) errorMsg = error.error.error;
        else if (error?.error?.message) errorMsg = error.error.message;
        else if (error.message) errorMsg = error.message;
        
        return throwError(() => ({ 
          message: errorMsg,
          originalError: error 
        }));
      })
    );
  }

  getHistoriqueCaissesByVendeur(vendeurId: number): Observable<CaisseResponse[]> {
    return this.usersService.getValidAccessToken().pipe(
      switchMap(token => {
        if (!token) throw new Error('Aucun token trouv');

        const headers = new HttpHeaders({
          'Authorization': `Bearer ${token}`
        });

        return this.http.get<CaisseResponse[]>(
          `${this.apiUrl}/vendeur/${vendeurId}`, 
          { headers }
        );
      }),
      catchError(error => {
        let errorMsg = 'Erreur lors du chargement de l\'historique des caisses';
        if (error?.error?.error) errorMsg = error.error.error;
        else if (error?.error?.message) errorMsg = error.error.message;
        else if (error.message) errorMsg = error.message;
        
        return throwError(() => ({ 
          message: errorMsg,
          originalError: error 
        }));
      })
    );
  }
  

}